                </div>
                <ol>

                </ol>
            </div>
        </div>
        <div>

            <section class="mb-4">


                <h2 class="h1-responsive font-weight-bold text-center my-4" style="color: #d40219;">Contact us</h2>

                <p class="text-center w-responsive mx-auto mb-5" style="color: #d40219;">Do you have any questions? Please
                    do not hesitate to
                    contact us
                    directly. Our team will come back to you within
                    a matter of hours to help you.</p>

                <div class="row">
                    <div class="contact-image col-md-4" id="imgs">
                        <img src="../assets/contact.png" alt="rocket_contact"  />
                    </div>
                    <div class="col-md-5 mb-md-0 mb-5">
                        <form @submit.prevent="submitContact">

                            <div class="form-outline mb-4">
                                <input type="text" required class="form-control" v-model="names" placeholder="Enter Your Name Here"/>
                                <label class="form-label" for="form4Example1">Name</label>
                            </div>
                            <div class="form-outline mb-4">
                                <input type="number" required class="form-control" v-model="phoneno" placeholder="Enter Your Phone Number :+91" />
                                <label class="form-label" for="form4Example1">Phone Number</label>
                            </div>

                            <div class="form-outline mb-4">
                                <input type="email" required class="form-control" v-model="email" placeholder="abc@gmail.com" />
                                <label class="form-label" for="form4Example2">Email address</label>
                            </div>


                            <div class="form-outline mb-4">
                                <textarea class="form-control" rows="4" required v-model="msg" placeholder="Enter Your Query"></textarea>
                                <label class="form-label" for="form4Example3">Message</label>
                            </div>





                            <button type="submit" class="btn btn-danger btn-block mb-4">Send</button>
                        </form>


