<script>
import axios from "axios";
export default {
    data() {
        return {
            phoneno:'',
            names: '',
            email: '',
            msg: ''
        }
    },
    methods: {
        async submitContact() {

            try {
                const response = await axios.post('https://rentalcar.herokuapp.com//contact', {
                    phoneno:this.phoneno,
                    email:this.email,
                    names: this.names,
                    msg:this.msg
                });
                console.log(response.data);
                alert("Your message is succefully saved")

                // Display success message to user or redirect to success page
            } catch (error) {
                console.error(error);
                alert(error);
            }

        },
       
        confirm() {
            alert("Thanks for reaching towards us.We will contact you soon!")
        }

    }
}
</script>
