<template>
    <div>
        <div class="position-fixed" style="bottom: 20px; right: 20px; z-index: 5000;">
         
                <button class="bot-btn fas fa-robot" data-toggle="modal" data-target="#chatBot"></button>
 


        </div>

        <div class="modal fade" id="chatBot" tabindex="-1" role="dialog" aria-labelledby="chatbot" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="car1ModalLabel">RideX Bot</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="container mt-5">
                                <div class="card">
                                    <div class="card-header">
                                        <center>
                                            <h5 class="card-title"> Ask a Question</h5>
                                        </center>
                                    </div>
                                    <div class="card-body">
                                        <div class="form-group">
                                            <label for="question">Question:</label>
                                            <input type="text" id="question" class="form-control" v-model="userQuestion">
                                        </div>
                                        <div class="form-group">
                                            <button class="btn btn-danger" @click="submitQuestion">Submit</button>
                                        </div>
                                        <div v-if="answer" class="alert alert-success mt-3">
                                            {{ answer }}
                                        </div>
                                        <div v-if="error" class="alert alert-danger mt-3">
                                            {{ error }}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-12">
                        <div class="modal-footer col-md-12">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

export default {
    data() {
        return {

            userQuestion: '',
            predefinedQnA: [
                {
                    question: 'What is your name',
                    answer: 'My name is ChatBot.'
                },
                {
                    question: 'My transaction failed',
                    answer: 'Dont worry, contact us either by fillig form or calling on +91 7666943359.'
                },
                {
                    question: 'how to book the car',
                    answer: 'Login>>Renatl Package>> Book Now>> Delivery...'
                },
                {
                    question: 'how to contact you',
                    answer: 'You can contact us by contact us section or calling us on number +91 7666943359 or by reaching towards our offices loaction '
                },
                {
                    question: 'hello',
                    answer: `hey how can i help you`
                }
            ],
            answer: '',
            error: ''
        };
    },
    methods: {

        submitQuestion() {
            const result = this.predefinedQnA.find(qna => qna.question === this.userQuestion)
            if (result) {
                this.answer = result.answer
                this.error = ''
            } else {
                this.answer = ''
                this.error = 'Sorry, I don\'t know the answer to that question.'
            }
        },

    }
}
</script>


<style scoped>
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css'); 

.bot-btn {
  font-size: 54px;
  color: black; 
  background-color: transparent;
  border: none;
  transition: 0.1s;
}
.bot-btn:hover{
    color:#d40219
}
.card {
    max-width: 500px;
    margin: 0 auto;
}

.card-header {
    background-color: #007bff;
    color: #fff;
}

.btn-primary {
    background-color: #007bff;
    border-color: #007bff;
}

.btn-primary:hover {
    background-color: #0069d9;
    border-color: #0062cc;
}

.alert-success {
    background-color: #d4edda;
    border-color: #c3e6cb;
    color: #155724;
}

.alert-danger {
    background-color: #f8d7da;
    border-color: #f5c6cb;
    color: #721c24;
}


.bot-btn:hover {
    transition: 1s;

}
</style>

