<script>
export default {
  data() {
    return {
      chatMessages: [],
      intro: [
        "Hello, I am Parking Assistant, How can i help you?",
        "Hi, I am a virtual assistant, What i can do for you?",
        "Hello, My name is ParkMyRide,How can i help you? ",
      ],
      help: [
        "How may i assist you?",
        "How can i help you?",
        "What i can do for you?",
      ],
      greetings: [
        "i am good you little piece of love",
        "i am fine, what about you",
        "don't want to talk",
        "i am good",
      ],
      hobbies: [
        "i love to talk with humans",
        "i like to make friends like you",
        "i like cooking",
      ],
      pizzas: [
        "which type of pizza do you like?",
        "i can make a pizza for you",
        "i would love to make a pizza for you",
        "would you like cheese pizza?",
      ],
      thank: ["Most welcome", "Not an issue", "Its my pleasure", "Mention not"],
      closing: [
        "Ok bye-bye",
        "As you wish, bye take-care",
        "Bye-bye, see you soon..",
      ],
    };
  },
  methods: {
    showUserMsg(userMsg) {
      this.chatMessages.push({ text: userMsg, sender: "user" });
    },
    showChatbotMsg(chatbotMsg) {
      this.chatMessages.push({ text: chatbotMsg, sender: "chatbot" });
    },
    chatbotVoice(message) {
      const speech = new SpeechSynthesisUtterance();
      speech.text = "This is test message";
      let response = "";
      if (message.includes("hello"||"hey")) {
        response = this.intro[Math.floor(Math.random() * this.intro.length)];
      } else if (message.includes("hi")) {
        response = this.help[Math.floor(Math.random() * this.help.length)];
      } else if (message.includes("how are you" || "how are you doing today")) {
        response =
          this.greetings[Math.floor(Math.random() * this.greetings.length)];
      } else if (
        message.includes(
          "tell me something about you"
        )
      ) {
        response =
          this.hobbies[Math.floor(Math.random() * this.hobbies.length)];
      } else if (message.includes("pizza")) {
        response = this.pizzas[Math.floor(Math.random() * this.pizzas.length)];
      } else if (message.includes("thank you" || "thank you so much")) {
        response = this.thank[Math.floor(Math.random() * this.thank.length)];
      } else if (message.includes("talk to you")) {
        response =
          this.closing[Math.floor(Math.random() * this.closing.length)];
      }
      speech.text =
        response ||
        "I'm sorry, I didn't understand that. Can you please rephrase?";
      window.speechSynthesis.speak(speech);
      return response;
    },
    activateMic() {
      const { webkitSpeechRecognition } = window;
      const recognition = new webkitSpeechRecognition();
      recognition.lang = "en-US";
      recognition.start();
      recognition.onresult = (event) => {
        const speechResult = event.results[0][0].transcript;
        this.showUserMsg(speechResult);
        const chatbotResponse = this.chatbotVoice(speechResult);
        if (chatbotResponse) {
          this.showChatbotMsg(chatbotResponse);
        } else {
          setTimeout(() => {
            this.showChatbotMsg(
              "I'm sorry, I didn't understand that. Can you please rephrase?"
            );
          }, 1000);
        }
      };
      recognition.onend = () => {
        console.log("Speech recognition service disconnected");
      };
    },
  },
};
</script>

