            generateReceipt() {
           
                const doc = new jsPDF();

                // Set up fonts
                doc.addFont('Helvetica-Bold.ttf', 'helvetica-bold', 'bold');
                doc.addFont('Helvetica.ttf', 'helvetica', 'normal');
                doc.setFontSize(12);

                // Set up colors
                const blueColor = [45, 122, 232];
                const grayColor = [0, 0, 0];
                const whiteColor = [255, 255, 255];

                // Add ParkMyRide branding
                doc.setFillColor(...grayColor);
                doc.rect(0, 0, 210, 50, 'F');
                doc.setTextColor(...blueColor);
                doc.setFont('helvetica-bold', 'normal');
                doc.text('CarLelo', 85, 20);
                doc.setFont('helvetica', 'normal');
                doc.setFontSize(8);
                doc.text('Only One Solution to Rental Probllems .CarLelo.com...', 77, 27);
                doc.setTextColor(...whiteColor);

                // Add receipt title
                doc.setFillColor(...blueColor);
                doc.rect(20, 40, 170, 10, 'F');
                doc.setTextColor(...whiteColor);
                doc.setFont('helvetica-bold', 'normal');
                doc.setFontSize(14);
                doc.text('Booking Receipt', 80, 47);

                // Add transaction details
                doc.setDrawColor(...grayColor);
                doc.setLineWidth(0.1);
                doc.line(20, 60, 190, 60);
                doc.setFont('helvetica', 'normal');
                doc.setFontSize(12);
                doc.setTextColor(...blueColor);
                doc.text('Car ID:', 20, 70);
                doc.setTextColor(...grayColor);
                doc.text(`${this.userId}`, 80, 70);
                doc.setTextColor(...blueColor);
                doc.text('Owner Name:', 20, 80);
                doc.setTextColor(...grayColor);
                doc.text(`${this.Customer_Name}`, 80, 80);
                doc.setTextColor(...blueColor);
                doc.text('Owner Phone Number:', 20, 90);
                doc.setTextColor(...grayColor);
                doc.text(`${this.phoneno}`, 80, 90);
                doc.setTextColor(...blueColor);
                doc.text('Owner Address:', 20, 100);
                doc.setTextColor(...grayColor);
                doc.text(`${this.Customer_address}`, 80, 100);
                doc.setTextColor(...blueColor);
                doc.text('Vehicle Model:', 20, 110);


                doc.setTextColor(...grayColor);
                doc.text(`${ this.car.model }`, 80, 110);
                doc.setTextColor(...blueColor);
                doc.text('Vehicle Name:', 20, 120);
                doc.setTextColor(...grayColor);
                doc.text(`${ this.car.name }`, 80, 120);
                doc.setTextColor(...blueColor);
                doc.text('Delivery Address:', 20, 130);
                doc.setTextColor(...grayColor);
                doc.text(`${this.dropaddress }`, 80, 130);
                doc.setTextColor(...blueColor);
                doc.text('Fees:', 20, 140);
                doc.setTextColor(...grayColor);
                doc.text(`Rs.${ this.car.price }`, 80, 140);

                // Add contact information
                doc.setTextColor(...blueColor);
                doc.setFontSize(8);
                doc.text('Contact us:', 20, 160);
                doc.setTextColor(...grayColor);
                doc.text('Phone: +91-1234567890', 20, 165);
                doc.text('Email: carlelo264@gmail.com', 20, 170);

                // Save the PDF file with a custom filename
                doc.save(`CarLelo_Booking_Recepit${ this.userId }.pdf`);
   
        },





        async submitForm() {

            try {
                const response = await axios.post('https://rentalcar.herokuapp.com/buynow', {
                    IdproofNo: this.IdproofNo,
                    section: this.section,
                    carname: this.car.name,
                    carmodel: this.car.model,
                    email: this.email,
                    price: this.car.price,
                    userId: this.userId,
                    Customer_Name: this.Customer_Name,
                    phoneno: this.phoneno,
                    Customer_address: this.Customer_address,
                    dropaddress: this.dropaddress,
                });
                console.log(response.data);
                alert("Car is Booked .")
                if (response.status === 201) {
                    alert("Car is Booked Successfully");
                }
                this.generateReceipt();

                // Display success message to user or redirect to success page
            } catch (error) {
                console.error(error);
                alert("Car is not avaliable now");
                // Display error message to user or redirect to error page
            }
        },


        showBookNow() {
            document.getElementById('bookNowModa1').style.display = "block";
            document.getElementById('bookinginfo1').style.display = "block";

        },
        showBookingInfo() {
            document.getElementById("bookNowModa1").style.display = "none";
            document.getElementById("bookinginfo1").style.display = "none"
        },
        shareCar(carId) {
            event.preventDefault;
            const url = window.document.location.href; // get the current URL
            console.log(url);
            const carUrl = `${url}`; // add the car ID to the URL
            console.log(carId);
            console.log(carUrl);
            // let cardemoid=carId
            // console.log(cardemoid);
            const message = encodeURIComponent(`Check out this car:${carUrl}`);
            const whatsappUrl = `https://wa.me/?text=${message}`;

            window.open(whatsappUrl, '_blank'); // open the WhatsApp share window
            // }, goto(){
            //     this.$router.push("/user/:userId");
            // }
        },
        shareOnInstagram() {
            event.preventDefault();

            // Create the message to be shared
            const message = encodeURIComponent("Check out this awesome product on my website!");
            const productUrl = window.document.location.href; // Replace with the actual product URL

            // Combine the message and product URL
            const shareUrl = `${productUrl}?message=${message}`;

            // Open the Instagram direct message page
            const instagramUrl = `https://www.instagram.com/direct/new/?=${encodeURIComponent(shareUrl)}`;
            window.open(instagramUrl, '_blank');
        },

        booktoogle() {
            document.getElementById("booktooggle").style.display = "block"
        }

    }
};
