      rating: '',
      name: "",
      review: '',
      subsection:'',
     
    };
  },
  components: {

 
    RentNavbar
},
  methods: {
    async Submitreview() {
      try {
        const response = await axios.post('http://127.0.0.1:3000/rentsreview', {
          section:this.section,
          rating: this.rating,
          name: this.name,
          email:this.email,
          review: this.review,
          subsection:this.subsection
        });
        console.log(response.data);
        alert("Review is Submitted")

        // Display success message to user or redirect to success page
      } catch (error) {
        console.error(error);
        alert(error);
      }
    },
 
  
  },
};
