<template>
    <form @submit.prevent="submitPayment">
      <label for="quantity">Quantity:</label>
      <input type="number" id="quantity" v-model="quantity" required>
      <button type="submit">Submit Payment</button>
    </form>
  </template>
  
  <script>
  import Stripe from 'stripe';
  
  export default {
    data() {
      return {
        quantity: 1,
      };
    },
    methods: {
      async submitPayment() {
        try {
          const response = await fetch('/create-checkout-session', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ quantity: this.quantity }),
          });
          const { id } = await response.json();
          const stripe = Stripe('pk_test_51Mg0NdSHh8opwA07AOyZFPbT1itF9MpUj8ctptmISZULjb6hRFrIBfZl9APR1DLcTMTVN1xKF5a2XligzekI8fxk00VZlEM5mi');
          const { error } = await stripe.redirectToCheckout({ sessionId: id });
          if (error) {
            console.error(error);
          }
        } catch (e) {
          console.error(e);
        }
      },
    },
  };
  </script>
  