<template>
  <div class="body">
    <div class="container" id="container">
      <div class="form-container sign-up-container">
        <form @submit.prevent="signauthentication" method="post">
          <h1>Create Account</h1>
          <div class="social-container">
            <a href="https://www.facebook.com/FreeWebsiteCode/" class="social"
              ><i class="fab fa-facebook-f"></i
            ></a>
            <a href="https://twitter.com/freewebsitecode" class="social"
              ><i class="fab fa-twitter"></i
            ></a>
            <a
              href="https://www.linkedin.com/in/freewebsitecode/"
              class="social"
              ><i class="fab fa-linkedin-in"></i
            ></a>
          </div>

          <input type="text" placeholder="Name"  v-model="name"/>
          <input type="email" placeholder="Email" v-model="email"/>
          <input type="password" placeholder="Password" v-model="password"/>
          <input type="number" placeholder="PhoneNo" v-model="phoneno" />
          <input type="text" placeholder="Address" v-model="address" />
          <button type="submit">Sign Up</button>
        </form>
      </div>
      <div class="form-container sign-in-container">
        <form @submit.prevent="loginauthentication" method="post">
          <h1>Login</h1>
          <div class="social-container">
            <a href="https://www.facebook.com/FreeWebsiteCode/" class="social"
              ><i class="fab fa-facebook-f"></i
            ></a>
            <a href="https://twitter.com/freewebsitecode" class="social"
              ><i class="fab fa-twitter"></i
            ></a>
            <a
              href="https://www.linkedin.com/in/freewebsitecode/"
              class="social"
              ><i class="fab fa-linkedin-in"></i
            ></a>
          </div>
          <span>or use your account</span>
          <input type="email" placeholder="Email" v-model="email"/>
          <input type="password" placeholder="Password"  v-model="password"/>

          <button type="submit">Login</button>
        </form>
      </div>
      <div class="overlay-container">
        <div class="overlay">
          <div class="overlay-panel overlay-left">
            <h1>Welcome Back!</h1>
            <p>
              To keep connected with us please login with your personal info
            </p>
            <button class="ghost" id="signIn" @click="togglelogin">
              Login
            </button>
          </div>
          <div class="overlay-panel overlay-right">
            <h1>Hello, Friend!</h1>
            <p>Enter your personal details and start journey with us</p>
            <button class="ghost" id="signUp" @click="togglesignin">
              Sign Up
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      isShow: true,
      name: "",
      email: "",
      phoneno: "",
      password: "",

      address: "",
    };
  },
  methods: {
    togglesignin() {
      const signUpButton = document.getElementById("signUp");
      const container = document.getElementById("container");
      container.classList.add("right-panel-active");
      signUpButton.addEventListener("click", () => {
        container.classList.add("right-panel-active");
      });
    },
    togglelogin() {
      const signInButton = document.getElementById("signIn");
      const container = document.getElementById("container");
      container.classList.remove("right-panel-active");
      signInButton.addEventListener("click", () => {
        container.classList.remove("right-panel-active");
      });
    },
    async signauthentication() {
      try {
        const response = await axios.post("http://127.0.0.1:3000/signup", {
          name: this.name,
          email: this.email,
          password: this.password,
          phoneno: this.phoneno,
          address: this.address,
        });
        console.log(response.data);
        alert("Your Account is created successful");
        this.isShow = !this.isShow;
        // Display success message to user or redirect to success page
      } catch (error) {
        console.error(error);
        alert("Email id Already Exist");
        // Display error message to user or redirect to error page
      }
    },

    async loginauthentication() {
      try {
        const response = await axios.post("http://127.0.0.1:3000/login", {
          email: this.email,
          password: this.password,
        });
        this.$router.push("/home");
        console.log(response.data);
        alert(`Welcome!!! Explore some car.`);

        // Store authentication token in local storage and redirect to dashboard
      } catch (error) {
        console.error(error);
        alert("Failed to login");
        // Display error message to user or redirect to error page
      }
    },
  },
};
</script>


<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,800");

* {
  box-sizing: border-box;
}

.body {

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  height: 100vh;
  margin: -20px 0 50px;
}

h1 {
  font-weight: bold;
  margin: 0;
}

h2 {
  text-align: center;
}

p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

span {
  font-size: 12px;
}

a {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

button {
  border-radius: 20px;
  border: 1px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

button:active {
  transform: scale(0.95);
}

button:focus {
  outline: none;
}

button.ghost {
  background-color: transparent;
  border-color: #ffffff;
}

form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.container {
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 680px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #d40219;
  background: -webkit-linear-gradient(to right, #ff4b2b,#d40219);
  background: linear-gradient(to right, #ff4b2b,#d40219);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

@media only screen and (max-width: 500px) {
  /* CSS rules for mobile phones go here */
  form {
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    width:100%;
    height: 100%;
    text-align: center;
  }

  input {
    background-color: #eee;
    border: none;
  
    height: 100%;
    width: 180%;
  }

  .container {
    background-color: #fff;

    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

    width: 1000px;
    max-width: 100%;
    max-height: 100%;
  }
}
</style>