
<template>
    <div class="container my-4">
        <div class="row">

            <div class="card col-md-5">

                <img :src="car.imageUrl" class="card-img-top" alt="Car Image"><br>
                <br><br>
                <div class="row col-md-12">
                    <div class="col-md-6">

                        <img :src="car.imageUrl2" class="card-img-top" alt="Car Image">
                    </div>

                    <div class="col-md-6">
                        <img :src="car.imageUrl2" class="card-img-top" alt="Car Image">

                    </div>
                </div>

            </div>
            <div class="card-body col-md-7">

                <h2 class="card-title">{{ car.name }} {{ car.model }}</h2>
                <h2 class="card-title">Car Price:{{ car.price }}</h2>
                <p class="card-text"><strong>Color:</strong> {{ car.color }}</p>
                <p class="card-text"><strong>Horsepower/Cc:</strong> {{ car.horsepower }} </p>
                <h3>Car specification</h3>
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Name</th>
                            <th scope="col">Specification</th>

                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">1</th>
                            <td>Top Speed</td>
                            <td> {{ car.topSpeed }} mph</td>

                        </tr>
                        <tr>
                            <th scope="row">2</th>
                            <td>0-60 mph </td>
                            <td> {{ car.zeroToSixty }} seconds</td>

                        </tr>
                        <tr>
                            <th scope="row">3</th>
                            <td>Transmission</td>
                            <td>{{ car.transmission }}</td>

                        </tr>
                        <tr>
                            <th scope="row">4</th>
                            <td>Fuel Economy</td>
                            <td>{{ car.mileage }} kmpl</td>

                        </tr>
                        <tr>
                            <th scope="row">5</th>
                            <td>Engine</td>
                            <td>{{ car.engine }} cc</td>

                        </tr>
                        <tr>
                            <th scope="row">6</th>
                            <td>Fuel Type</td>
                            <td>{{ car.fuel }}</td>

                        </tr>
                        <tr>
                            <th scope="row">7</th>
                            <td>Seater</td>
                            <td>{{ car.seat }}</td>

                        </tr>
                    </tbody>
                </table>
                <button class="btn btn-primary details-btn" data-toggle="modal" data-target="#carshare">Share </button>
                <button class="btn btn-danger " @click="booktoogle"> Book Now</button>
            </div>




        </div>
        <div class="modal fade" id="carshare" tabindex="-1" role="dialog" aria-labelledby="car1ModalLabel"
