            generateReceipt() {
             
                const doc = new jsPDF();

                // Set up fonts
                doc.addFont('Helvetica-Bold.ttf', 'helvetica-bold', 'bold');
                doc.addFont('Helvetica.ttf', 'helvetica', 'normal');
                doc.setFontSize(12);

                // Set up colors
                const blueColor = [45, 122, 232];
                const blackColor = [0, 0, 0];
                const whiteColor = [255, 255, 255];

                // Add ParkMyRide branding
                doc.setFillColor(...blackColor);
                doc.rect(0, 0, 210, 50, 'F');
                doc.setTextColor(...blueColor);
                doc.setFont('helvetica-bold', 'normal');
                doc.text('CarLelo', 85, 20);
                doc.setFont('helvetica', 'normal');
                doc.setFontSize(8);
                doc.text('Only One Solution to Rental Probllems .CarLelo.com...', 77, 27);
                doc.setTextColor(...whiteColor);

                // Add receipt title
                doc.setFillColor(...blueColor);
                doc.rect(20, 40, 170, 10, 'F');
                doc.setTextColor(...whiteColor);
                doc.setFont('helvetica-bold', 'normal');
                doc.setFontSize(14);
                doc.text('Booking Receipt', 80, 47);

                // Add transaction details
                doc.setDrawColor(...blackColor);
                doc.setLineWidth(0.1);
                doc.line(20, 60, 190, 60);
                doc.setFont('helvetica', 'normal');
                doc.setFontSize(12);
                doc.setTextColor(...blueColor);
                doc.text('Car ID:', 20, 70);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.userId}`, 80, 70);
                doc.setTextColor(...blueColor);
                doc.text('Pick Up Location:', 20, 80);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.pickupLocation }`, 80, 80);
                doc.setTextColor(...blueColor);
                doc.text('Drop Location:', 20, 90);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.dropLocation }`, 80, 90);
                doc.setTextColor(...blueColor);
                doc.text('Pick Up Date:', 20, 100);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.pickupDate }`, 80, 100);
                doc.setTextColor(...blueColor);
                doc.text('Drop Date:', 20, 110);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.dropDate }`, 80, 110);

                doc.setTextColor(...blueColor);
                doc.text('Section:', 20, 120);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.section }`, 80, 120);

                doc.setTextColor(...blueColor);
                doc.text('Total Rate:', 20, 130);
                doc.setTextColor(...blackColor);
                doc.text(`Rs.${ this.totalRate }`, 80, 130);

                doc.setTextColor(...blueColor);
                doc.text('Driver Age:', 20, 140);
                doc.setTextColor(...blackColor);
                doc.text(`${ this.driverAge }`, 80, 140);
                // Add contact information
                doc.setTextColor(...blueColor);
                doc.setFontSize(8);
                doc.text('Contact us:', 20, 160);
                doc.setTextColor(...blackColor);
                doc.text('Phone: +91-1234567890', 20, 165);
                doc.text('Email: carLelo264@gmail.com', 20, 170);

                // Save the PDF file with a custom filename
                doc.save(`CarLelo_Booking_Receipt_${this.userId}.pdf`);
         

        },
        calculateTotalRate() {
            const pickup = new Date(this.pickupDate)
            const drop = new Date(this.dropDate)
            const diffInTime = drop.getTime() - pickup.getTime()
            const diffInDays = diffInTime / (1000 * 3600 * 24)

            //-----Rate Per day
            if (this.section === 'Sedan') {

                this.ratePerDay = this.sedanRate

            } else if (this.section === 'Suv') {

                this.ratePerDay = this.suvRate

            } else if (this.section === "Family") {

                this.ratePerDay = this.familyRate

            } else if (this.section === "Luxurious") {

                this.ratePerDay = this.luxuriousRate

            } else if (this.section === "E_Bike") {

                this.ratePerDay = this.ebikeRate
            } else if (this.section === "Scooty") {

                this.ratePerDay = this.scootyRate

            } else if (this.section === "Sport_Bike") {

                this.ratePerDay = this.sportRate

            }


            //-----Total rate
            let rate = 0
            if (this.section === 'Sedan') {
                rate = this.sedanRate
                this.ratePerDay = this.sedanRate
            } else if (this.section === 'Suv') {
                rate = this.suvRate
                this.ratePerDay = this.suvRate
            } else if (this.section === "Family") {
                rate = this.familyRate
                this.ratePerDay = this.familyRate
            } else if (this.section === "Luxurious") {
                rate = this.luxuriousRate
                this.ratePerDay = this.luxuriousRate
            } else if (this.section === "E_Bike") {
                rate = this.ebikeRate
                this.ratePerDay = this.ebikeRate
            } else if (this.section === "Scooty") {
                rate = this.scootyRate
                this.ratePerDay = this.scootyRate
            } else if (this.section === "Sport_Bike") {
                rate = this.sportRate
                this.ratePerDay = this.sportRate
            }

            this.totalRate = rate * diffInDays
        },

        async submitForm() {

            this.calculateTotalRate();
            const currentDate = new Date();
            const dropDateCutoff = new Date(currentDate.getFullYear(), currentDate.getMonth(), currentDate.getDate());

            const selectedDate = new Date(this.dropDate);
            try {
                if (selectedDate < dropDateCutoff) {
                    alert("Please choose a future date.");
                    return;
                }

                const response = await axios.post('http://127.0.0.1:3000/bookings', {
                    car_name: this.car.name,
                    car_model: this.car.model,
                    section: this.section,
                    totalRate: this.totalRate,
                    email: this.email,
                    userId: this.userId,
                    pickupLocation: this.pickupLocation,
                    dropLocation: this.dropLocation,
                    pickupDate: this.pickupDate,
                    dropDate: this.dropDate,
                    driverAge: this.driverAge,
                    IdproofNo: this.IdproofNo
                });

                console.log(currentDate);
                console.log(this.dropDate);
                console.log(response.data);
                alert("Car is Booked .");

                if (response.status === 201) {

                    alert("Booking created successfully");
                }
                this.generateReceipt()
                // Display success message to user or redirect to success page
            } catch (error) {
                console.error(error);
                alert("Car is not available now");
                // Display error message to user or redirect to error page
            }
        },

        showBookNow() {
            document.getElementById('bookNowModa1').style.display = "block";
            document.getElementById('bookinginfo1').style.display = "block";

        },
        showBookingInfo() {
            document.getElementById("bookNowModa1").style.display = "none";
            document.getElementById("bookinginfo1").style.display = "none"
        },
        shareCar(carId) {
            event.preventDefault;
            const url = window.document.location.href; // get the current URL
            console.log(url);
            const carUrl = `${url}`; // add the car ID to the URL
            console.log(carId);
            console.log(carUrl);
            // let cardemoid=carId
            // console.log(cardemoid);
            const message = encodeURIComponent(`Check out this car:${carUrl}`);
            const whatsappUrl = `https://wa.me/?text=${message}`;

            window.open(whatsappUrl, '_blank'); // open the WhatsApp share window
            // }, goto(){
            //     this.$router.push("/user/:userId");
            // }
        },

        booktoogle() {
            document.getElementById("booktooggle").style.display = "block"
        }

    },
    watch: {
        pickupDate: function () {
            this.calculateTotalRate()
        },
        dropDate: function () {
            this.calculateTotalRate()
        },
        section: function () {
            this.calculateTotalRate()
        },
    },
};
